'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Sidebar } from '@/components/layout/sidebar';
import { Topbar } from '@/components/layout/topbar';
import { NotificationProvider } from '@/app/context/notification-context';
import { isAuthenticated } from '@/lib/auth';

export default function AuthenticatedLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const router = useRouter();

    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        // Check authentication on mount
        if (!isAuthenticated()) {
            router.push('/');
        }
    }, [router]);

    // Don't render until mounted to avoid hydration mismatch
    if (!mounted) {
        return null;
    }

    // Don't render if not authenticated
    if (!isAuthenticated()) {
        return null;
    }

    return (
        <NotificationProvider>
            <div className="min-h-screen bg-background" suppressHydrationWarning>
                <Sidebar />
                <Topbar />
                <main className="pt-16 md:pl-64 min-h-screen transition-all duration-300">
                    <div className="container mx-auto p-6 max-w-7xl animate-in fade-in slide-in-from-bottom-4 duration-500">
                        {children}
                    </div>
                </main>
            </div>
        </NotificationProvider>
    );
}
