'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useLanguage } from '@/app/context/language-context';
import { LayoutDashboard, Receipt, Settings, LogOut, BarChart3, Wallet, Repeat, Target, FolderOpen, PieChart, TrendingUp } from 'lucide-react';
import { cn } from '@/lib/utils';

export function Sidebar() {
  const pathname = usePathname();
  const { t } = useLanguage();

  const navItems = [
    { name: t('Dashboard'), href: '/dashboard', icon: LayoutDashboard },
    { name: t('Income'), href: '/income', icon: TrendingUp },
    { name: t('Expenses'), href: '/expenses', icon: Receipt },
    { name: t('Categories'), href: '/categories', icon: FolderOpen },
    { name: t('Budgets'), href: '/budgets', icon: Wallet },
    { name: t('Recurring'), href: '/recurring', icon: Repeat },
    { name: t('Goals'), href: '/goals', icon: Target },
    { name: t('Reports'), href: '/reports', icon: BarChart3 },
    { name: t('AI Assistant'), href: '/ai', icon: PieChart },
    { name: t('Settings'), href: '/settings', icon: Settings },
  ];

  return (
    <aside className="hidden md:flex flex-col w-64 h-screen fixed left-0 top-0 bg-card border-r border-border z-50" suppressHydrationWarning>
      <div className="p-6 flex items-center gap-3">
        <img src="/logo.png" alt="NDFolio" className="h-8 w-8" />
        <h1 className="text-xl font-bold text-primary">NDFolio</h1>
      </div>

      <nav className="flex-1 px-4 py-4 space-y-2">
        {navItems.map((item) => {
          const isActive = pathname === item.href;
          return (
            <Link
              key={item.href}
              href={item.href}
              className={cn(
                "flex items-center gap-3 px-4 py-3 rounded-xl transition-all duration-200 group",
                isActive
                  ? "bg-primary/10 text-primary font-medium"
                  : "text-muted-foreground hover:bg-muted hover:text-foreground"
              )}
            >
              <item.icon className={cn("h-5 w-5", isActive ? "text-primary" : "text-muted-foreground group-hover:text-foreground")} />
              {item.name}
            </Link>
          );
        })}
      </nav>

      <div className="p-4 border-t border-border">
        <button className="flex items-center gap-3 px-4 py-3 w-full rounded-xl text-muted-foreground hover:bg-destructive/10 hover:text-destructive transition-colors">
          <LogOut className="h-5 w-5" />
          {t('Logout')}
        </button>
      </div>
    </aside>
  );
}
